* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 18px;
}

.page__body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.container {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .container {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --columns: 4;
  }
}

.card {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
}

.card__number {
  font-size: 16px;
  font-weight: 700;
}

.card__rating {
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  line-height: 14px;
}

.card__link {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  padding: 11px 69px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color, color .3s;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

